<template>
    <div id="top">
        <a-layout>
            <a-layout-header class="header" :style="{ position: 'fixed', zIndex: 1, width: '100%' }">
                <div class="logo">
                    {{ currentUser }}
                </div>
                <a-menu theme="dark" mode="horizontal" :style="{ lineHeight: '64px' }"
                        @click="onMenuClick">
                    <a-menu-item key="create">
                        <a-icon type="fork" />
                        <span></span>
                    </a-menu-item>
                    <a-menu-item key="list">
                        <a-icon type="dashboard" />
                        <span> </span>
                    </a-menu-item>
                    <a-menu-item key="detail" disabled>
                        <a-icon type="edit" />
                        <span></span>
                    </a-menu-item>
                    <a-menu-item key="listinstance" class="ant-menu-item-active">
                        <a-icon type="history" />
                        <span></span>
                    </a-menu-item>
                </a-menu>
            </a-layout-header>
            <a-layout-content>
                <div class="row">
                    <div class="col-1">
                    </div>
                    <div class="col-3">
                        <input class="form-control" v-model="search" type="text" placeholder="" />
                    </div>
                    <div class="input-group-append">
                        <button class="btn btn-outline-info" @click="advList" ></button>
                    </div>
                </div>
                <a-table id="tasktable" rowKey="id"
                         :columns="columns"
                         :dataSource="instances"
                         :pagination="pagination"
                         @change="onTableChange">
                    <span slot="detail" slot-scope="text, record">
                        <router-link v-bind:to="'/log/' + record.schedule_name + '/instance'"></router-link>
                    </span>

                </a-table>
            </a-layout-content>
            <!--
            <a-layout-footer>
                Copyright  2020-2021 CooTek Big Data Platform
            </a-layout-footer>
            -->
        </a-layout>
    </div>
</template>

<script>

    export default {
        data: function () {
            return {
                search: "",
                columns: [
                    {
                        title: "",
                        dataIndex: "type",
                    }, {
                        title: "",
                        dataIndex: "name",
                    }, {
                        title: "",
                        dataIndex: "schedule_name",
                    },{
                        title: "",
                        dataIndex: "status",
                    }, {
                        title: "id",
                        dataIndex: "job_id",
                    }, {
                        title: "",
                        dataIndex: "owner"
                    }, {
                        title: "",
                        dataIndex: "created",
                    }, {
                        title: "",
                        dataIndex: "updated",
                    }, {
                        title: "",
                        dataIndex: "id",
                        scopedSlots: {
                            customRender: "detail"
                        }
                    }
                ],
                //table
                current: 1,
            }
        },
        computed: {
            pagination: function () {
                return {
                    total: this.$store.state.totalInst,
                    current: this.current,
                };
            },
            instances: function () {
                return this.$store.state.instances;
            },
            currentUser: function () {
                return this.$store.state.username;
            }
        },
        methods: {
            advList() {
                this.$store.commit("setCurrentPagInst", 1);
                let req = {
                    search: this.search,
                    from: "search",
                };
                this.$store.dispatch("ListInstanceAction", req);
                this.current = 1;
            },
            onTableChange(e) {
                this.current = e.current;
                this.$store.commit("setCurrentPagInst", e.current);
                let req = {
                    search: this.search,
                    from: "list",
                };
                this.$store.dispatch("ListInstanceAction", req);
            },
            onMenuClick(e) {
                this.$router.push("/" + e.key);
            },
        },
        created() {
            this.$store.dispatch("CurrentUserAction");
            let req = {
                search: this.search,
                from: "created",
            };
            this.$store.dispatch("ListInstanceAction", req);
        }
    }
</script>

<style scoped>
    #top .ant-layout-header {
        background: white;
    }

    #top .ant-layout-content {
        padding: 0 50px;
        margin-top: 64px;
        padding-bottom: 60px;
        background-color: white;
    }

    #top .ant-layout-footer {
        background: white;
        text-align: center;
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 60px;
        clear: both;
    }

    #top .logo {
        width: 240px;
        height: 31px;
        float: left;
        color: white
    }
</style>