<template>
    <div id="top">
        <a-layout>
            <a-layout-header class="header" :style="{ position: 'fixed', zIndex: 1, width: '100%' }">
                <div class="logo">
                    {{ currentUser }}
                </div>
                <a-menu theme="dark" mode="horizontal" :style="{ lineHeight: '64px' }"
                        @click="onMenuClick">
                    <a-menu-item key="create">
                        <a-icon type="fork" />
                        <span></span>
                    </a-menu-item>
                    <a-menu-item key="list">
                        <a-icon type="dashboard" />
                        <span> </span>
                    </a-menu-item>
                    <a-menu-item key="detail" class="ant-menu-item-active">
                        <a-icon type="edit" />
                        <span></span>
                    </a-menu-item>
                    <a-menu-item key="listinstance">
                        <a-icon type="history" />
                        <span></span>
                    </a-menu-item>
                </a-menu>
            </a-layout-header>
            <a-layout-content>
                <a-modal v-model="dlgShow" title="" @ok="onDlgOK">
                    <a-form-model :model="form">
                        <a-form-item label="">
                            <a-radio-group v-model="form.trigger">
                                <a-radio-button value="cron"></a-radio-button>
                                <a-radio-button value="interval"></a-radio-button>
                                <a-radio-button value="date"></a-radio-button>
                            </a-radio-group>
                        </a-form-item>
                        <a-form-item v-if="form.trigger == 'interval'" label=" ">
                            <a-input v-model="form.interval_trigger_args" placeholder="" />
                        </a-form-item>
                        <a-form-item v-if="form.trigger == 'date'" label="">
                            <a-date-picker v-model="form.date_trigger_args" show-time valueFormat="yyyy-MM-DD HH:mm:ss"
                                           placeholder=""></a-date-picker>
                        </a-form-item>
                        <a-form-item v-if="this.form.trigger == 'cron'" label="">
                            <a-select v-model="form.cron_trigger_args.month" mode="multiple" placeholder="">
                                <a-select-option v-for="(item, index) in month_list" :key="index" :value="index + 1">{{ item }}</a-select-option>
                            </a-select>
                        </a-form-item>
                        <a-form-item v-if="this.form.trigger == 'cron'" label="">
                            <a-select v-model="form.cron_trigger_args.day" mode="tags"
                                      placeholder="">
                                <a-select-option value="last"></a-select-option>
                                <a-select-option disabled value="s1">*/2 2</a-select-option>
                                <a-select-option disabled value="s2">1-7 17</a-select-option>
                                <a-select-option disabled value="s3">1,7 17</a-select-option>
                                <a-select-option disabled value="s4">1-7/2 172</a-select-option>
                                <a-select-option disabled value="s5">(1-31)</a-select-option>
                            </a-select>
                        </a-form-item>
                        <a-form-item v-if="this.form.trigger == 'cron'" label="">
                            <a-select v-model="form.cron_trigger_args.hour" mode="tags"
                                      placeholder="">
                                <a-select-option value="s1" disabled>*/2  2</a-select-option>
                                <a-select-option value="s2" disabled>8-20 88</a-select-option>
                                <a-select-option value="s3" disabled>8,20 88</a-select-option>
                                <a-select-option value="s4" disabled>8-20/2 882</a-select-option>
                                <a-select-option value="s5" disabled>(0-23)</a-select-option>
                            </a-select>
                        </a-form-item>
                        <a-form-item v-if="this.form.trigger == 'cron'" label="">
                            <a-select v-model="form.cron_trigger_args.minute" mode="tags"
                                      placeholder="">
                                <a-select-option value="s1" disabled>*/5 5</a-select-option>
                                <a-select-option value="s2" disabled>0-30 30</a-select-option>
                                <a-select-option value="s3" disabled>0,30 30</a-select-option>
                                <a-select-option value="s4" disabled>0-30/5 5</a-select-option>
                                <a-select-option value="s5" disabled>(0-59)</a-select-option>
                            </a-select>
                        </a-form-item>
                        <a-form-item v-if="this.form.trigger == 'cron'" label="">
                            <a-select v-model="form.cron_trigger_args.day_of_week" mode="tags"
                                      placeholder="">
                                <a-select-option value="s1" disabled>5 </a-select-option>
                                <a-select-option value="s2" disabled>1-5 </a-select-option>
                                <a-select-option value="s3" disabled>1,2,3 </a-select-option>
                                <a-select-option value="s4" disabled>(0-6)06</a-select-option>
                            </a-select>
                        </a-form-item>
                        <a-form-item v-if="this.form.trigger == 'cron'" label="">
                            <a-range-picker show-time v-model="form.cron_trigger_args.range" type="daterange" valueFormat="yyyy-MM-DD HH:mm:ss" ></a-range-picker>
                        </a-form-item>
                    </a-form-model>
                </a-modal>

                <table class="table" v-if="ready">
                    <thead>
                        <tr>
                            <th></th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>ID</td>
                            <td>{{ task.id }}</td>
                        </tr>
                        <tr>
                            <td></td>
                            <td>{{ task.name }}</td>
                        </tr>
                        <tr>
                            <td></td>
                            <td>{{ task.user }}</td>
                        </tr>
                        <tr>
                            <td></td>
                            <td>{{ task.desc }}</td>
                        </tr>
                        <tr>
                            <td></td>
                            <td>
                                <div class="form-group">
                                    <label for="schdTypes"></label>
                                    <select disabled id="schdTypes" class="form-control" v-model="selType">
                                        <option v-for="t in schdTypes" :key="t">
                                            {{ t }}
                                        </option>
                                    </select>
                                    <a :href="doc" target="_blank" v-show="selType!=''"></a>
                                </div>
                                <div class="form-group">
                                    <label for="schdArgs"></label>
                                    <input type="text" class="form-control" v-model="$v.selArgs.$model" disabled />
                                    <validation-error v-bind:validation="$v.selArgs" />
                                </div>
                                <div class="form-group mt-0">
                                    <button class="btn btn-outline-warning" @click="setTaskTrigger"></button>
                                    <a-switch checked-children="" un-checked-children="" :checked="checked" @change="switchTrigger" />
                                </div>
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td>
                                <div class="form-group">
                                    <textarea class="form-control" rows="10" v-model="task.cmd">
                            </textarea>
                                    <button class="btn btn-outline-info" @click="runCmd"></button>
                                    <button class="btn btn-outline-warning" @click="updateCmd"></button>
                                </div>
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td>
                                <table class="table table-bordered " v-if="histories">
                                    <thead>
                                        <tr>
                                            <td></td>
                                            <td> s</td>
                                            <td></td>
                                            <td></td>
                                            <td></td>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="h in histories" v-bind:key="h.id">
                                            <td>{{ h.created  }}</td>
                                            <td>{{ h.taken }}</td>
                                            <td>{{ h.code }}</td>
                                            <td>
                                                <router-link target="_self" v-bind:to="'/log/' + h.id + '/stdout' ">stdout</router-link>
                                            </td>
                                            <td>
                                                <router-link target="_self" v-bind:to="'/log/' + h.id + '/stderr' ">stderr</router-link>
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                                <span v-else></span>
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td>
                                <button class="btn btn-danger" @click="deleteTask"></button>

                            </td>
                        </tr>
                    </tbody>
                </table>
            </a-layout-content>
        </a-layout>
    </div>
</template>

<script>
    import { required } from "vuelidate/lib/validators";
    import ValidationError from "./ValidationError";

    const month_list = ['', '', '', '', '', '', '', '', '', '', '', ''];

    export default {
        components: { ValidationError },
        validations: {
            selArgs: {
                required
            }
        },
        data: function () {
            return {
                task: null,
                selType: "",
                selArgs: "",
                schdTypes: [
                    "cron",
                    "interval",
                    "date",
                ],
                typeDocs: {
                    "cron": "https://apscheduler.readthedocs.io/en/stable/modules/triggers/cron.html#module-apscheduler.triggers.cron",
                    "interval": "https://apscheduler.readthedocs.io/en/stable/modules/triggers/interval.html#module-apscheduler.triggers.interval",
                    "date": "https://apscheduler.readthedocs.io/en/stable/modules/triggers/date.html#module-apscheduler.triggers.date",
                },
                ready: false,
                checked: false,
                dlgShow: false,
                month_list: month_list,
                form: {
                    trigger: "",
                    interval_trigger_args: "",
                    date_trigger_args: "",
                    cron_trigger_args: { month: [], day: [], hour: [], minute: [], day_of_week: [], range: [] },
                },
                
            }
        },
        computed: {
            doc() {
                return this.typeDocs[this.selType];
            },
            histories() {
                return this.$store.state.histories;
            },
            currentUser: function () {
                return this.$store.state.username;
            }
        },
        created() {
            this.$store.dispatch("CurrentUserAction");

            let taskId = this.$route.params.id;

            let t = this.$store.state.tasks.find(x => x.id == taskId);
            if (t == undefined) {
                this.$store.watch(state => state.actionTaskOrErr,
                    (t) => {
                        if (typeof (t) === 'string') {
                            this.$message.error(': ' + t, 3);
                        } else {
                            this.task = {};
                            Object.assign(this.task, t);
                            if (this.task.trigger) {
                                this.selType = this.task.trigger;
                            }
                            if (this.task.trigger_args) {
                                this.selArgs = this.task.trigger_args;
                            }
                            this.ready = true;
                            this.checked = this.task.enabled;
                            this.$store.dispatch("GetTaskHistoryAction", taskId)
                        }
                    }
                );

                this.$store.dispatch("GetTaskAction", taskId);
            } else { 
                this.task = {};
                Object.assign(this.task, t);
                if (this.task.trigger) {
                    this.selType = this.task.trigger;
                }
                if (this.task.trigger_args) {
                    this.selArgs = this.task.trigger_args;
                }
                this.ready = true;
                this.checked = this.task.enabled;

                this.$store.dispatch("GetTaskHistoryAction", taskId)
            }
        },

        methods: {
            deleteTask() {
                this.$store.dispatch("DeleteTaskAction", this.task);
                this.$router.replace("/list")
            },
            switchTrigger(check) {
                this.checked = check;
                this.task.enabled = check;
                const request = {
                    id: this.task.id,
                    checked: check,
                };
                this.$store.dispatch("SwitchTriigerAction", request);
            },
            runCmd() {
                let unwatch = this.$store.watch(state => state.runSetMsg,
                    (msg) => {
                        const arr = msg.split("|");
                        const message = arr[1];
                        if (message === "") {
                            this.$message.info("", 3);
                        } else {
                            this.$message.error(": " + message, 3);
                        }
                        unwatch();
                    }
                );

                this.$store.dispatch("RunCmdAction", this.task.cmd);
            },
            updateCmd() {
                let unwatch = this.$store.watch(state => state.taskSetMsg,
                    (msg) => {
                        const arr = msg.split("|");
                        const message = arr[1];
                        if (message === "") {
                            this.$message.info("cmd ", 3);
                        } else {
                            this.$message.error("cmd : " + message, 3);
                        }
                        unwatch();
                    }
                );

                this.$store.dispatch("UpdateTaskAction", this.task);
            },
            onDlgOK() {
                if (this.form.trigger === '') {
                    this.$message.error("please select a kind of trigger type", 3);
                    return;
                }

                let args = "";

                if (this.form.trigger === 'interval') {
                    args = this.form.interval_trigger_args.trim();
                    if (args === '') {
                        this.$message.error("interval need args", 3);
                        return;
                    }
                    let num = parseInt(args, 10);
                    if (isNaN(num)) {
                        this.$message.error("interval args must be integer", 3);
                        return;
                    }
                    if (num < 1) {
                        this.$message.error("interval args must be positive(number) second", 3);
                        return;
                    }

                    let numStr = num + '';
                    if (numStr.length != args.length) {
                        this.$message.error("interval args must be integer", 3);
                        return;
                    }
                }
                if (this.form.trigger === 'date') {
                    args = this.form.date_trigger_args;
                    if (args === '') {
                        this.$message.error("date need args", 3);
                        return;
                    }
                }

                if (this.form.trigger == 'cron') {
                    args = ";"; //year
                    args += this.handleCronArgs(this.form.cron_trigger_args.month); //month
                    args += this.handleCronArgs(this.form.cron_trigger_args.day);  //day
                    args += ';';  //week
                    args += this.handleCronArgs(this.form.cron_trigger_args.day_of_week);  //day_of_week
                    args += this.handleCronArgs(this.form.cron_trigger_args.hour);  //hour
                    args += this.handleCronArgs(this.form.cron_trigger_args.minute);  //minute
                    args += ';';  //second
                    if (this.form.cron_trigger_args.range && this.form.cron_trigger_args.range.length) {
                        args += this.form.cron_trigger_args.range[0] + ';'; //start_date
                        args += this.form.cron_trigger_args.range[1]    //end_date
                    } else {
                        args += ';'
                    }
                }
                this.selType = this.form.trigger;
                this.selArgs = args;
                this.dlgShow = false;

                let req = {
                    id: this.task.id,
                    trigger: this.selType,
                    trigger_args: this.selArgs,
                };

                let unwatch = this.$store.watch(state => state.triggerSetMsg,
                    (msg) => {
                        const arr = msg.split("|");
                        const message = arr[1];
                        if (message === "") {
                            this.$message.info("trigger ", 3);
                        } else {
                            this.$message.error("trigger : " + message, 3);
                        }
                        unwatch();
                    }
                );

                this.$store.dispatch("SetTaskTriggerAction", req);
            },
            setTaskTrigger() {
                this.dlgShow = true;
            },
            onMenuClick(e) {
                if (e.key != "detail") {
                    this.$router.push("/" + e.key);
                }
            },
            handleCronArgs(args) {
                if (args.length) {
                    return args.join(',') + ';'
                } else {
                    return ';'
                }
            },
        }
    }
</script>

<style scoped>

    #top .ant-layout-header {
        background: white;
    }

    #top .ant-layout-content {
        padding: 0 50px;
        margin-top: 64px;
        padding-bottom: 60px;
        background-color: white;
    }

    #top .ant-layout-footer {
        background: white;
        text-align: center;
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 60px;
        clear: both;
    }

    #top .logo {
        width: 240px;
        height: 31px;
        float: left;
        color: white
    }
</style>
