<template>
    <div id="top">
        <a-layout>
            <a-layout-content>
                <a-row>
                    <a-col :span="8">
                        <a-button type="primary" @click="back">
                            
                        </a-button>
                    </a-col>
                    <a-col :span="8">
                        <h2></h2>
                    </a-col>
                </a-row>
                <hr />

                <pre v-if="type !== 'instance'">
{{ log }}
                </pre>
                <pre  v-else>
{{ instanceLog }}
                </pre>
            </a-layout-content>
            <a-layout-footer>
                Copyright  2020-2021 CooTek Big Data Platform
            </a-layout-footer>
        </a-layout>
    </div>
</template>

<script>
    export default {
        data: function () {
            return {
                log: "",
                type: null,
            }
        },
        computed: {
            instanceLog() {
                return this.$store.state.lastInstanceLogs;
            }
        },
        methods: {
            back() {
                this.$router.go(-1)
            }
        },
        created() {
            this.$store.dispatch("CurrentUserAction");

            //_self store
            let id = this.$route.params.id;
            this. type = this.$route.params.type;
            if (this.type == "instance") {
                this.$store.dispatch("GetInstanceLogs", id)
                return
            }

            const histories = this.$store.state.histories;
            const h = histories.find(x => x.id == id)
            if (this.type === "stderr") {
                this.log = h.stderr;
            }
            if (this.type === "stdout") {
                this.log = h.stdout;
            }
            
        }
    }
</script>

<style scoped>
    #top .ant-layout-content {
        padding: 0 50px;
        padding-bottom: 60px;
        background-color: white;
    }

    #top .ant-layout-footer {
        background: white;
        text-align: center;
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 60px;
        clear: both;
    }

    pre {
        height: 560px;
    }
</style>
