<template>
    <div id="top">
        <a-layout>
            <a-layout-header class="header" :style="{ position: 'fixed', zIndex: 1, width: '100%' }">
                <div class="logo">
                    {{ currentUser }}
                </div>
                <a-menu theme="dark" mode="horizontal" :style="{ lineHeight: '64px' }"
                        @click="onMenuClick">
                    <a-menu-item key="create">
                        <a-icon type="fork" />
                        <span></span>
                    </a-menu-item>
                    <a-menu-item key="list" class="ant-menu-item-active">
                        <a-icon type="dashboard" />
                        <span> </span>
                    </a-menu-item>
                    <a-menu-item key="detail" disabled>
                        <a-icon type="edit" />
                        <span></span>
                    </a-menu-item>
                    <a-menu-item key="listinstance">
                        <a-icon type="history" />
                        <span></span>
                    </a-menu-item>
                </a-menu>
            </a-layout-header>
            <a-layout-content>
                <a-table id="tasktable" rowKey="id"
                         :columns="columns"
                         :dataSource="tasks"
                         :pagination="pagination"
                         @change="onTableChange">
                    <span slot="detail" slot-scope="text, record">
                        <router-link v-bind:to="'/detail/' + record.id"></router-link>
                    </span>

                </a-table>
            </a-layout-content>
            <!--
                <a-layout-footer>
                    Copyright  2020-2021 CooTek Big Data Platform
                </a-layout-footer>
            -->
            </a-layout>
        </div>
    </template>

    <script>
        export default {
            data: function () {
                return {
                    columns: [
                        {
                            title: "",
                            dataIndex: "name",
                        }, {
                            title: "",
                            width: 300,
                            dataIndex: "desc",
                        }, {
                            title: "",
                            dataIndex: "user",
                        }, {
                            title: "",
                            width: 600,
                            dataIndex: "cmd",
                        }, {
                            title: "",
                            dataIndex: "id",
                            scopedSlots: {
                                customRender: "detail"
                            }
                        }
                    ]
                }
            },
            computed: {
                pagination: function(){
                    return {
                        total: this.$store.state.total,
                    };
                },
                tasks: function() {
                    return this.$store.state.tasks;
                },
                currentUser: function () {
                    return this.$store.state.username;
                }
            },
            methods: {
                onTableChange(e) {
                    this.$store.commit("setCurrentPage", e.current);
                    this.$store.dispatch("listTaskAction");
                },
                onMenuClick(e) {
                    this.$router.push("/" + e.key);
                },
            },
            created() {
                this.$store.dispatch("CurrentUserAction");
                this.$store.dispatch("listTaskAction");
            }
        }
    </script>

    <style scoped>
        #tasktable {
            background: white;
        }

        #top .ant-layout-header {
            background: white;
        }
        #top .ant-layout-content {
            padding: 0 50px;
            margin-top: 64px;
            padding-bottom: 60px;
            background-color: white;
        }
        #top .ant-layout-footer {
            background: white;
            text-align: center;
            position: absolute;
            bottom: 0;
            width: 100%;
            height: 60px;
            clear: both;
        }
        #top .logo {
            width: 240px;
            height: 31px;
            float: left;
            color: white
        }
    </style>
