<template>
    <div id="top">
        <a-layout>
            <a-layout-header class="header" :style="{ position: 'fixed', zIndex: 1, width: '100%' }">
                <div class="logo">
                    {{ currentUser }}
                </div>
                <a-menu theme="dark" mode="horizontal" :style="{ lineHeight: '64px' }"
                        @click="onMenuClick">
                    <a-menu-item key="create" class="ant-menu-item-active">
                        <a-icon type="fork" />
                        <span></span>
                    </a-menu-item>
                    <a-menu-item key="list">
                        <a-icon type="dashboard" />
                        <span> </span>
                    </a-menu-item>
                    <a-menu-item key="detail" disabled>
                        <a-icon type="edit" />
                        <span></span>
                    </a-menu-item>
                    <a-menu-item key="listinstance">
                        <a-icon type="history" />
                        <span></span>
                    </a-menu-item>
                </a-menu>
            </a-layout-header>
            <a-layout-content>
                <a-alert v-if="createResult" message="  " show-icon type="success" />
                <a-alert v-if="createError" :message="lastCreatedError" show-icon type="error" />

                <a-form-model ref="taskFrom" :model="form" :rules="rules">
                    <a-form-model-item ref="taskName" label="" prop="taskName">
                        <a-input v-model="form.taskName"
                                 @blur="() => { $refs.taskName.onFieldBlur();}" />
                    </a-form-model-item>
                    <a-form-model-item ref="desc" label="" prop="taskDesc">
                        <a-input v-model="form.taskDesc"
                                 @blur="() => { $refs.desc.onFieldBlur();}" />
                    </a-form-model-item>
                    <a-form-model-item label="" prop="taskGroup">
                        <a-select v-model="form.taskGroup" placeholder="">
                            <a-select-option v-for="g in groups" :key="g" :value="g">
                                {{ g }}
                            </a-select-option>
                        </a-select>
                    </a-form-model-item>
                    <a-form-model-item ref="taskCmd" label="" prop="taskCmd">
                        <a-textarea v-model="form.taskCmd" rows="6"
                                    @blur="() => { $refs.taskCmd.onFieldBlur();}" />
                    </a-form-model-item>
                    <a-form-model-item>
                        <a-button type="primary" @click="onSubmit">
                            
                        </a-button>
                        <a-button style="margin-left: 10px;" @click="resetForm">
                            
                        </a-button>
                    </a-form-model-item>
                </a-form-model>
            </a-layout-content>
            <!--
                <a-layout-footer>
                    Copyright  2020-2021 CooTek Big Data Platform
                </a-layout-footer>
             -->
            </a-layout>
        </div>
    </template>

    <script>
        export default {
            data: function () {
                return {
                    groups: ["ad"],
                    form: {
                        taskName: "",
                        taskDesc: "",
                        taskCmd: "",
                        taskGroup: "",
                    },
                    rules: {
                        taskName: [
                            { required: true, message: '', trigger: 'blur' },
                        ],
                        taskDesc: [
                            { required: true, message: '', trigger: 'blur' },
                        ],
                        taskCmd: [
                            { required: true, message: '', trigger: 'blur' },
                        ],
                        taskGroup: [
                            { required: true, message: '', trigger: 'blur' },
                        ],
                    },
                    //
                    createResult: false,
                    //
                    createError: false,
                }
            },
            computed: {
                lastCreatedError() {
                    return this.$store.state.lastCreatedError
                },
                currentUser: function () {
                    return this.$store.state.username;
                }
            },
            methods: {
                onSubmit() {
                    this.$refs.taskFrom.validate(valid => {
                        if (valid) {
                            let t = {
                                name: this.form.taskName,
                                desc: this.form.taskDesc,
                                cmd: this.form.taskCmd,
                                group: this.form.taskGroup,
                            };

                            let unwatchOK = this.$store.watch(state => state.lastCreatedTaskId,
                                (nv) => {
                                    if (nv > 0) {
                                        this.createResult = true;
                                        this.createError = false;
                                    }
                                    unwatchOK();
                                })

                            let unwatchERR = this.$store.watch(state => state.lastCreatedError,
                                (nv) => {
                                    if (nv != "") {
                                        this.createError = true;
                                        this.createResult = false;
                                    }
                                    unwatchERR();
                                })

                            this.$store.dispatch("CreateTaskAction", t);
                        } else {
                            return false;
                        }
                    });
                },
                resetForm() {
                    this.$refs.taskFrom.resetFields();
                    this.createResult = false;
                    this.createError = false;
                },
                onMenuClick(e) {
                    this.$router.push("/" + e.key);
                },
            },
            created() {
                this.$store.dispatch("CurrentUserAction");
            }
        }
    </script>

    <style scoped>
        #top .ant-layout-header {
            background: white;
        }

        #top .ant-layout-content {
            padding: 0 50px;
            margin-top: 64px;
            padding-bottom: 60px;
            background-color: white;
        }

        #top .ant-layout-footer {
            background: white;
            text-align: center;
            position: absolute;
            bottom: 0;
            width: 100%;
            height: 60px;
            clear: both;
        }

        #top .logo {
            width: 240px;
            height: 31px;
            float: left;
            color: white
        }
    </style>
